import * as React from "react"
import { Button } from "@/components/ui/button"
import {
  Card,
  CardContent,
  CardFooter,
  CardHeader,
  CardTitle,
} from "@/components/ui/card"
import { Input } from "@/components/ui/input"
import { Label } from "@/components/ui/label"

// Example: Accepts an employee prop
type InfoCardProps = {
  employee: {
    id: string
    firstName: string
    lastName: string
    email?: string
    contactNumber: string
    role?: string
  }
  onEdit?: () => void
}

export function InfoCard({ employee, onEdit }: Readonly<InfoCardProps>) {
  const [editing, setEditing] = React.useState(false)
  const [form, setForm] = React.useState({
    firstName: employee.firstName,
    lastName: employee.lastName,
    role: employee.role ?? "",
    email: employee.email ?? "",
    contactNumber: employee.contactNumber,
  })

  const handleChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    setForm({ ...form, [e.target.name]: e.target.value })
  }

  const handleEditClick = () => setEditing(true)
  const handleConfirmClick = () => {
    setEditing(false)
    if (onEdit) onEdit()
    // Optionally: pass form data to parent here
  }

  return (
    <Card className="w-full max-w-sm">
      <CardHeader className="flex-row items-center justify-between">
        <CardTitle className="text-lg">
          Employee Details
        </CardTitle>
        {editing ? (
          <Button size="sm" variant="outline" onClick={handleConfirmClick}>Confirm</Button>
        ) : (
          <Button size="sm" variant="outline" onClick={handleEditClick}>Edit</Button>
        )}
      </CardHeader>
      <CardContent>
        <form className="flex flex-col gap-4" onSubmit={e => e.preventDefault()}>
          <div>
            <Label htmlFor="firstName">First Name</Label>
            <Input id="firstName" name="firstName" value={form.firstName} onChange={handleChange} readOnly={!editing} className="mt-1" />
          </div>
          <div>
            <Label htmlFor="lastName">Last Name</Label>
            <Input id="lastName" name="lastName" value={form.lastName} onChange={handleChange} readOnly={!editing} className="mt-1" />
          </div>
          <div>
            <Label htmlFor="role">Role</Label>
            <Input id="role" name="role" value={form.role} onChange={handleChange} readOnly={!editing} className="mt-1" />
          </div>
          <div>
            <Label htmlFor="email">Email</Label>
            <Input id="email" name="email" value={form.email} onChange={handleChange} readOnly={!editing} className="mt-1" />
          </div>
          <div>
            <Label htmlFor="contactNumber">Contact Number</Label>
            <Input id="contactNumber" name="contactNumber" value={form.contactNumber} onChange={handleChange} readOnly={!editing} className="mt-1" />
          </div>
        </form>
      </CardContent>
      <CardFooter>
        <div className="text-xs text-muted-foreground">Employee ID: {employee.id}</div>
      </CardFooter>
    </Card>
  )
}
